import React from 'react';
import ReactDOM from 'react-dom';
import {HashRouter, Route, Switch} from "react-router-dom";
import Structure from "./Structure";
import Home from "./pages/Home";
import About from "./pages/About";

import './index.css';

const renderView = (ToRender) => {
    return <Structure content={ToRender}/>
}

ReactDOM.render(
    <React.StrictMode>
        <HashRouter basename="/#">
            <Switch>
                <Route path={"/about"}>
                    {renderView(About)}
                </Route>
                <Route path={"/"}>
                    {renderView(Home)}
                </Route>
            </Switch>
        </HashRouter>
    </React.StrictMode>,
    document.getElementById('root')
);

